@font-face {
  font-family: 'F25_Bank_Printer';
  src: local('F25'), url('../fonts/F25_Bank_Printer.otf') format('opentype');
  font-weight: 400;
  font-style: monospace;
}

html, body, #app, #app>div {
  height: 100%;
}

#root {
  height: 100%;
}

:root {
  --bg-color: rgb(5, 4, 4)!important;
  --text-color: rgb(234, 234, 247);
  --inv-text-color: var(--bg-color);
  --inv-bg-color: var(--text-color);
}

header {
  margin: 0;
  font-family: F25_Bank_Printer, monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  margin: 0;
  font-family: F25_Bank_Printer, monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--bg-color);
  color: var(--text-color);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
